/**
 * ÁßªÂä®Á´ØÂ∑•ÂÖ∑Ê†èÁªÑ‰ª∂
 * Ëá™ÈÄÇÂ∫îÁöÑÂ∫ïÈÉ®Â∑•ÂÖ∑Ê†è
 */

import type { Editor } from '../../core/Editor'
import { EventEmitter } from '../../core/EventEmitter'
import { createLogger } from '../../utils/logger'

const logger = createLogger('MobileToolbar')

export interface ToolbarItem {
  id: string
  icon: string
  title: string
  type?: 'button' | 'separator'
  active?: boolean
  disabled?: boolean
  action?: (editor: Editor) => void
}

export interface MobileToolbarOptions {
  container: HTMLElement
  editor: Editor
  items?: ToolbarItem[]
  height?: string
  backgroundColor?: string
  activeColor?: string
  autoHide?: boolean
}

export class MobileToolbar extends EventEmitter {
  private container: HTMLElement
  private editor: Editor
  private toolbarElement: HTMLElement
  private itemsContainer: HTMLElement
  private moreMenu: HTMLElement | null = null
  private options: Required<MobileToolbarOptions>
  private items: Map<string, ToolbarItem> = new Map()
  private isVisible = true
  private isCompact = false
  private lastScrollY = 0

  constructor(options: MobileToolbarOptions) {
    super()
    this.container = options.container
    this.editor = options.editor
    this.options = {
      items: [],
      height: '50px',
      backgroundColor: '#ffffff',
      activeColor: '#2196F3',
      autoHide: true,
      ...options,
    }

    this.createToolbar()
    this.setupEventListeners()
    this.updateActiveStates()
  }

  /**
   * ÂàõÂª∫Â∑•ÂÖ∑Ê†èDOM
   */
  private createToolbar(): void {
    this.toolbarElement = document.createElement('div')
    this.toolbarElement.className = 'mobile-toolbar'
    this.toolbarElement.style.cssText = `
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
      height: ${this.options.height};
      background: ${this.options.backgroundColor};
      border-top: 1px solid #e0e0e0;
      z-index: 1000;
      display: flex;
      align-items: center;
      padding: 0 10px;
      transform: translateY(0);
      transition: transform 0.3s ease-out;
      box-shadow: 0 -2px 8px rgba(0,0,0,0.1);
    `

    // ÂÆâÂÖ®Âå∫ÂüüÈÄÇÈÖç
    this.toolbarElement.style.paddingBottom = 'env(safe-area-inset-bottom, 0)'

    // ÂàõÂª∫È°πÁõÆÂÆπÂô®
    this.itemsContainer = document.createElement('div')
    this.itemsContainer.className = 'toolbar-items'
    this.itemsContainer.style.cssText = `
      display: flex;
      align-items: center;
      gap: 5px;
      flex: 1;
      overflow-x: auto;
      -webkit-overflow-scrolling: touch;
      scrollbar-width: none;
    `
    this.itemsContainer.style.msOverflowStyle = 'none'
    this.itemsContainer.style.setProperty('::-webkit-scrollbar', 'display: none')

    // Ê∏≤ÊüìÂ∑•ÂÖ∑Ê†èÈ°π
    this.renderItems()

    this.toolbarElement.appendChild(this.itemsContainer)
    document.body.appendChild(this.toolbarElement)
  }

  /**
   * Ê∏≤ÊüìÂ∑•ÂÖ∑Ê†èÈ°π
   */
  private renderItems(): void {
    this.itemsContainer.innerHTML = ''
    this.items.clear()

    // Âú®Á¥ßÂáëÊ®°Âºè‰∏ãÔºåÂè™ÊòæÁ§∫Ê†∏ÂøÉÂäüËÉΩ
    const itemsToRender = this.isCompact
      ? this.options.items.filter(item => ['bold', 'italic', 'undo', 'redo', 'more'].includes(item.id))
      : this.options.items

    itemsToRender.forEach((item) => {
      if (item.type === 'separator') {
        this.itemsContainer.appendChild(this.createSeparator())
      }
      else {
        const element = this.createToolbarItem(item)
        this.itemsContainer.appendChild(element)
        this.items.set(item.id, { ...item, element } as any)
      }
    })
  }

  /**
   * ÂàõÂª∫Â∑•ÂÖ∑Ê†èÈ°π
   */
  private createToolbarItem(item: ToolbarItem): HTMLElement {
    const button = document.createElement('button')
    button.className = `toolbar-item toolbar-item-${item.id}`
    button.style.cssText = `
      min-width: 40px;
      height: 40px;
      border: none;
      background: transparent;
      color: ${item.disabled ? '#999' : '#333'};
      font-size: 18px;
      font-weight: bold;
      border-radius: 8px;
      cursor: ${item.disabled ? 'not-allowed' : 'pointer'};
      transition: background-color 0.2s, transform 0.1s;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0 10px;
      user-select: none;
      -webkit-tap-highlight-color: transparent;
      flex-shrink: 0;
    `

    if (item.active) {
      button.style.backgroundColor = `${this.options.activeColor}20`
      button.style.color = this.options.activeColor
    }

    button.innerHTML = item.icon
    button.title = item.title
    button.disabled = item.disabled || false

    if (!item.disabled) {
      // Ëß¶Êë∏ÂèçÈ¶à
      button.addEventListener('touchstart', () => {
        button.style.transform = 'scale(0.95)'
        button.style.backgroundColor = '#f0f0f0'
      })

      button.addEventListener('touchend', () => {
        button.style.transform = 'scale(1)'
        button.style.backgroundColor = item.active ? `${this.options.activeColor}20` : 'transparent'
      })

      // ÁÇπÂáªÂ§ÑÁêÜ
      button.addEventListener('click', () => {
        this.handleItemClick(item)
      })
    }

    return button
  }

  /**
   * ÂàõÂª∫ÂàÜÈöîÁ∫ø
   */
  private createSeparator(): HTMLElement {
    const separator = document.createElement('div')
    separator.className = 'toolbar-separator'
    separator.style.cssText = `
      width: 1px;
      height: 24px;
      background: #e0e0e0;
      margin: 0 5px;
      flex-shrink: 0;
    `
    return separator
  }

  /**
   * Â§ÑÁêÜÂ∑•ÂÖ∑Ê†èÈ°πÁÇπÂáª
   */
  private handleItemClick(item: ToolbarItem): void {
    logger.info('Toolbar item clicked:', item.id)

    // ÁâπÊÆäÂ§ÑÁêÜÊõ¥Â§öÊåâÈíÆ
    if (item.id === 'more') {
      this.showMoreMenu()
      return
    }

    // ÊâßË°åÈªòËÆ§Âä®‰Ωú
    if (item.action)
      item.action(this.editor)
    else
      this.executeCommand(item.id)

    // Êõ¥Êñ∞Áä∂ÊÄÅ
    this.updateActiveStates()

    // Ëß¶Âèë‰∫ã‰ª∂
    this.emit('itemclick', item)
  }

  /**
   * ÊâßË°åÁºñËæëÂô®ÂëΩ‰ª§
   */
  private executeCommand(command: string): void {
    switch (command) {
      case 'bold':
        this.editor.toggleBold()
        break
      case 'italic':
        this.editor.toggleItalic()
        break
      case 'underline':
        this.editor.toggleUnderline()
        break
      case 'undo':
        this.editor.undo()
        break
      case 'redo':
        this.editor.redo()
        break
      case 'image':
        this.showImagePicker()
        break
      case 'link':
        this.showLinkDialog()
        break
      default:
        logger.warn('Unknown command:', command)
    }
  }

  /**
   * ÊòæÁ§∫Êõ¥Â§öËèúÂçï
   */
  private showMoreMenu(): void {
    if (this.moreMenu) {
      this.hideMoreMenu()
      return
    }

    this.moreMenu = document.createElement('div')
    this.moreMenu.className = 'toolbar-more-menu'
    this.moreMenu.style.cssText = `
      position: fixed;
      bottom: ${Number.parseInt(this.options.height) + 10}px;
      right: 10px;
      background: white;
      border-radius: 8px;
      box-shadow: 0 2px 16px rgba(0,0,0,0.2);
      padding: 10px;
      z-index: 1001;
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 10px;
      min-width: 200px;
      animation: slideUp 0.2s ease-out;
    `

    // Ê∑ªÂä†Âä®Áîª
    const style = document.createElement('style')
    style.textContent = `
      @keyframes slideUp {
        from {
          opacity: 0;
          transform: translateY(10px);
        }
        to {
          opacity: 1;
          transform: translateY(0);
        }
      }
    `
    document.head.appendChild(style)

    // È¢ùÂ§ñÁöÑÂ∑•ÂÖ∑ÊåâÈíÆ
    const extraItems = [
      { id: 'strikethrough', icon: 'SÃ∂', title: 'Âà†Èô§Á∫ø' },
      { id: 'code', icon: '<>', title: '‰ª£Á†Å' },
      { id: 'quote', icon: '"', title: 'ÂºïÁî®' },
      { id: 'list', icon: '‚ò∞', title: 'ÂàóË°®' },
      { id: 'heading', icon: 'H', title: 'Ê†áÈ¢ò' },
      { id: 'table', icon: '‚äû', title: 'Ë°®Ê†º' },
      { id: 'emoji', icon: 'üòä', title: 'Ë°®ÊÉÖ' },
      { id: 'color', icon: 'üé®', title: 'È¢úËâ≤' },
    ]

    extraItems.forEach((item) => {
      const button = document.createElement('button')
      button.style.cssText = `
        width: 40px;
        height: 40px;
        border: none;
        background: #f5f5f5;
        border-radius: 8px;
        font-size: 16px;
        cursor: pointer;
        transition: background-color 0.2s;
      `
      button.innerHTML = item.icon
      button.title = item.title

      button.addEventListener('click', () => {
        this.executeCommand(item.id)
        this.hideMoreMenu()
      })

      this.moreMenu!.appendChild(button)
    })

    document.body.appendChild(this.moreMenu)

    // ÁÇπÂáªÂÖ∂‰ªñÂú∞ÊñπÂÖ≥Èó≠
    setTimeout(() => {
      document.addEventListener('click', this.hideMoreMenu.bind(this), { once: true })
    }, 100)
  }

  /**
   * ÈöêËóèÊõ¥Â§öËèúÂçï
   */
  private hideMoreMenu(): void {
    if (this.moreMenu) {
      this.moreMenu.remove()
      this.moreMenu = null
    }
  }

  /**
   * ÊòæÁ§∫ÂõæÁâáÈÄâÊã©Âô®
   */
  private showImagePicker(): void {
    const input = document.createElement('input')
    input.type = 'file'
    input.accept = 'image/*'
    input.addEventListener('change', (e) => {
      const file = (e.target as HTMLInputElement).files?.[0]
      if (file) {
        const reader = new FileReader()
        reader.onload = (e) => {
          const url = e.target?.result as string
          this.editor.insertImage(url)
        }
        reader.readAsDataURL(file)
      }
    })
    input.click()
  }

  /**
   * ÊòæÁ§∫ÈìæÊé•ÂØπËØùÊ°Ü
   */
  private showLinkDialog(): void {
    const url = prompt('ËØ∑ËæìÂÖ•ÈìæÊé•Âú∞ÂùÄ:')
    if (url) {
      const text = prompt('ËØ∑ËæìÂÖ•ÈìæÊé•ÊñáÂ≠ó:') || url
      this.editor.insertLink(url, text)
    }
  }

  /**
   * ËÆæÁΩÆ‰∫ã‰ª∂ÁõëÂê¨Âô®
   */
  private setupEventListeners(): void {
    // ÁõëÂê¨ÁºñËæëÂô®Áä∂ÊÄÅÂèòÂåñ
    this.editor.on('selectionchange', () => {
      this.updateActiveStates()
    })

    // Ëá™Âä®ÈöêËóèÂäüËÉΩ
    if (this.options.autoHide) {
      let scrollTimeout: any

      window.addEventListener('scroll', () => {
        const currentScrollY = window.scrollY

        if (currentScrollY > this.lastScrollY && currentScrollY > 100) {
          // Âêë‰∏ãÊªöÂä®ÔºåÈöêËóèÂ∑•ÂÖ∑Ê†è
          this.hide()
        }
        else {
          // Âêë‰∏äÊªöÂä®ÔºåÊòæÁ§∫Â∑•ÂÖ∑Ê†è
          this.show()
        }

        this.lastScrollY = currentScrollY

        // ÊªöÂä®ÂÅúÊ≠¢ÂêéÊòæÁ§∫Â∑•ÂÖ∑Ê†è
        clearTimeout(scrollTimeout)
        scrollTimeout = setTimeout(() => {
          this.show()
        }, 500)
      }, { passive: true })
    }
  }

  /**
   * Êõ¥Êñ∞Ê¥ªÂä®Áä∂ÊÄÅ
   */
  private updateActiveStates(): void {
    // Êõ¥Êñ∞Âä†Á≤óÁä∂ÊÄÅ
    this.setItemActive('bold', document.queryCommandState('bold'))
    this.setItemActive('italic', document.queryCommandState('italic'))
    this.setItemActive('underline', document.queryCommandState('underline'))

    // Êõ¥Êñ∞Êí§ÈîÄ/ÈáçÂÅöÁä∂ÊÄÅ
    this.setItemEnabled('undo', this.editor.canUndo())
    this.setItemEnabled('redo', this.editor.canRedo())
  }

  /**
   * ËÆæÁΩÆÈ°πÁõÆÊøÄÊ¥ªÁä∂ÊÄÅ
   */
  setItemActive(id: string, active: boolean): void {
    const item = this.items.get(id)
    if (item) {
      item.active = active
      const element = (item as any).element as HTMLElement
      if (active) {
        element.style.backgroundColor = `${this.options.activeColor}20`
        element.style.color = this.options.activeColor
      }
      else {
        element.style.backgroundColor = 'transparent'
        element.style.color = '#333'
      }
    }
  }

  /**
   * ËÆæÁΩÆÈ°πÁõÆÂêØÁî®Áä∂ÊÄÅ
   */
  setItemEnabled(id: string, enabled: boolean): void {
    const item = this.items.get(id)
    if (item) {
      item.disabled = !enabled
      const element = (item as any).element as HTMLButtonElement
      element.disabled = !enabled
      element.style.color = enabled ? '#333' : '#999'
      element.style.cursor = enabled ? 'pointer' : 'not-allowed'
    }
  }

  /**
   * ËÆæÁΩÆÁ¥ßÂáëÊ®°Âºè
   */
  setCompactMode(compact: boolean): void {
    if (this.isCompact !== compact) {
      this.isCompact = compact
      this.renderItems()
      logger.info('Compact mode:', compact)
    }
  }

  /**
   * ÊòæÁ§∫Â∑•ÂÖ∑Ê†è
   */
  show(): void {
    if (!this.isVisible) {
      this.isVisible = true
      this.toolbarElement.style.transform = 'translateY(0)'
      this.emit('show')
    }
  }

  /**
   * ÈöêËóèÂ∑•ÂÖ∑Ê†è
   */
  hide(): void {
    if (this.isVisible) {
      this.isVisible = false
      this.toolbarElement.style.transform = 'translateY(calc(100% + env(safe-area-inset-bottom, 0)))'
      this.emit('hide')
    }
  }

  /**
   * ÂàáÊç¢ÊòæÁ§∫Áä∂ÊÄÅ
   */
  toggle(): void {
    if (this.isVisible)
      this.hide()
    else
      this.show()
  }

  /**
   * Ëé∑ÂèñÊòæÁ§∫Áä∂ÊÄÅ
   */
  getVisible(): boolean {
    return this.isVisible
  }

  /**
   * ÈîÄÊØÅÂ∑•ÂÖ∑Ê†è
   */
  destroy(): void {
    this.hideMoreMenu()
    this.toolbarElement.remove()
    this.removeAllListeners()
  }
}
